.weather {
    background-image:url('https://cdn2.cloudpro.co.uk/sites/cloudprod7/files/2018/07/cloud_0.jpg') ;
   height: 1000px;
    width: 100%;
    padding: 0px 20px 20px 20px;
    
  }
  .weather .title {
    font-size: 4rem;
  
    font-family: "Times New Roman", Times, serif;
  }
  .weather form {
    margin-top: 30px;
  }
  .weather form input {
    padding: 10px;
    border: none;
    border-bottom: 2px dashed rgb(2, 57, 88);
    margin-right: 10px;
  }
  
  .weather form input:focus {
    outline: none;
    border-bottom: 3px dashed rgb(2, 57, 88);
  }
  
  .weather .getweather {
    margin-left: 10px;
    padding: 8px;
    font-size: 1.3rem;
    border: none;
    border-radius: 20px;
    border: 2px solid black;
    background: rgb(2, 57, 88, 0.8);
    cursor: pointer;
    color: whitesmoke;
  }
  
  .weather .getweather:hover {
    background: transparent;
    color: black;
  }
  
  .weather .getweather:focus {
    outline: none;
  }
  